.card {
  border: 4px rgb(99, 99, 99) solid;
  border-radius: 2px;
  width: 150px;
  height: 150px;
  margin: 1rem;
  position: relative;
  box-shadow: 4px 4px 10px rgb(56, 56, 56);
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:rgb(248, 248, 248);
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  box-shadow: 8px 8px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}
img{
  margin-left: 21px
}
nav {
  width: 100%;
  font-size: 3rem;
  color: #fff;
  margin-bottom: 50px;
  background-color: rgb(56, 56, 56);
  height: auto;
  font-family: 'Bungee', cursive;
}

a {
  color: #fed420;
}

a:hover {
  color: #fed420;
  text-decoration: none;
}

nav li {
  display: inline-block;
  padding: 26px 0;
  width: 25%;
  /* text-align: center; */
}

ul {
  /* padding-left: 0px; */
  display: flex;
  align-items: center;
}
.correct {
  -webkit-animation: color-change 1s infinite ;
}

@-webkit-keyframes color-change {
  0% { color: rgba(118, 119, 37, 0.76); }
  50% { color: rgba(238, 255, 0, 0.918); }
  100% { color: rgba(168, 159, 31, 0.212); }
}
.incorrect {
  color: rgba(226, 6, 6, 0.747);
}


.wrapper {
  background: rgb(175, 175, 175);
  background-image: url('http://www.infinityjuventus.net/8/images/background-1513246253.jpg');
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  padding-bottom: 50px;
}
.title {
  width: 100%;
  text-align: right;
  font-size: 2rem;
  color:#fed420;
  margin-bottom: 50px;
  padding-left: 3%;
  padding-right: 3%;  
  text-shadow: 1px 2px gray;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
* {
  box-sizing: border-box;
  font-family: 'Black Ops One', cursive;

}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
